.ribbon1 {
    position: absolute;
    inset-block-start: -6.1px;
    inset-inline-end: 10px;
    color: $white;
    z-index: 1;
    &:after {
      position: absolute;
      content: "";
      width: 0;
      height: 0;
      border-inline-start: 20px solid transparent;
      border-inline-end: 24px solid transparent;
      border-block-start: 13px solid $danger;
    }
    span {
      position: relative;
      display: block;
      text-align: center;
      background: $danger;
      font-size: $font-size-14;
      line-height: 1;
      padding: 12px 8px 10px;
      border-start-end-radius: 8px;
      &:before,
      &:after {
        position: absolute;
        content: "";
      }
      &:before {
        height: 6px;
        width: 6px;
        inset-inline-start: -6px;
        inset-block-start: 0;
        background: $danger;
      }
      &:after {
        height: 6px;
        width: 8px;
        inset-inline-start: -8px;
        inset-block-start: 0;
        border-radius: 8px 8px 0 0;
        background: $danger;
      }
    }
  }
  .ribbone {
    width: 100%;
    position: relative;
    background-size: cover;
    text-transform: uppercase;
    color: $white;
    width: 150px;
    height: 150px;
    overflow: hidden;
    position: absolute;
    z-index: 6;
    &::after {
      position: absolute;
      z-index: -1;
      content: "";
      display: block;
      border: 5px solid;
    }
    &::before {
      position: absolute;
      z-index: -1;
      content: "";
      display: block;
      border: 5px solid;
    }
    span {
      position: absolute;
      display: block;
      width: 225px;
      padding: 3px 0;
      box-shadow: 0 5px 10px rgba(0,0,0, 0.1);
      color: $white;
      text-shadow: 0 1px 1px rgba(0,0,0, 0.1);
      text-transform: capitalize;
      text-align: center;
      font-size: 11px;
    }
  }
