.border-container {
    display: inline-block;
    width: 5rem;
    height: 5rem;
    margin: 0.25rem;
    background-color: $light;
}
.border {
    border: 1px solid $border-color !important;
    &.border-dashed {
        border-style: dashed !important;
    }
    &.border-dotted {
        border-style: dotted !important;
    }
    &.border-primary {
        border: 1px solid $primary !important;
        opacity: 1;
        &.border-opacity-10 {
            border-color: rgba($primary, 0.1) !important;
        }
        &.border-opacity-25 {
            border-color: rgba($primary, 0.25) !important;
        }
        &.border-opacity-50 {
            border-color: rgba($primary, 0.50) !important;
        }
        &.border-opacity-75 {
            border-color: rgba($primary, 0.75) !important;
        }
        &.border-opacity-100 {
            border-color: rgba($primary,1) !important;
        }
    }
    &.border-secondary {
        border: 1px solid $secondary !important;
        opacity: 1;
        &.border-opacity-10 {
            border-color: rgba($secondary,0.1) !important;
        }
        &.border-opacity-25 {
